<template>
    <div class="all-content__paginate">
        <div class="box-1">

            <div class="paginate_item" @click="changePageStore">
                <div class="paginate_item__wrapper">
                    <div class="paginate_item__wrapper__icon" id="paginate-icon-1" data-value="1"></div>
                    <div class="paginate_item__wrapper__text" data-value="1">TBC Classic</div>
                </div>
            </div>
            
            <div class="paginate_item" @click="changePageStore">
                <div class="paginate_item__wrapper">
                    <div class="paginate_item__wrapper__icon" id="paginate-icon-2" data-value="2"></div>
                    <div class="paginate_item__wrapper__text" data-value="2">Torghast</div>
                </div>
            </div>

            <div class="paginate_item" @click="changePageStore">
                <div class="paginate_item__wrapper">
                    <div class="paginate_item__wrapper__icon" id="paginate-icon-3" data-value="3"></div>
                    <div class="paginate_item__wrapper__text" data-value="3">Farming</div>
                </div>
            </div>

            <div class="paginate_item" @click="changePageStore">
                <div class="paginate_item__wrapper">
                    <div class="paginate_item__wrapper__icon" id="paginate-icon-4" data-value="4"></div>
                    <div class="paginate_item__wrapper__text" data-value="4">Raids</div>
                </div>
            </div>

            <div class="paginate_item" @click="changePageStore">
                <div class="paginate_item__wrapper">
                    <div class="paginate_item__wrapper__icon" id="paginate-icon-5" data-value="5"></div>
                    <div class="paginate_item__wrapper__text" data-value="5">PvP</div>
                </div>
            </div>

            
        </div>
        <div class="box-2">

            <div class="paginate_item" @click="changePageStore">
                <div class="paginate_item__wrapper">
                    <div class="paginate_item__wrapper__icon" id="paginate-icon-6" data-value="6"></div>
                    <div class="paginate_item__wrapper__text" data-value="6">Shadowlands</div>
                </div>
            </div>
            
            <div class="paginate_item" @click="changePageStore">
                <div class="paginate_item__wrapper">
                    <div class="paginate_item__wrapper__icon" id="paginate-icon-7" data-value="7"></div>
                    <div class="paginate_item__wrapper__text" data-value="7">Castle Nathria</div>
                </div>
            </div>

            <div class="paginate_item" @click="changePageStore">
                <div class="paginate_item__wrapper">
                    <div class="paginate_item__wrapper__icon" id="paginate-icon-8" data-value="8"></div>
                    <div class="paginate_item__wrapper__text" data-value="8">Gearing</div>
                </div>
            </div>

            <div class="paginate_item" @click="changePageStore">
                <div class="paginate_item__wrapper">
                    <div class="paginate_item__wrapper__icon" id="paginate-icon-9" data-value="9"></div>
                    <div class="paginate_item__wrapper__text" data-value="9">Mounts</div>
                </div>
            </div>


        </div>
    </div>
</template>

<script>

export default{
    name:'radio-buttons-mobail',
    data(){
        return{
        }
    },
    methods: {
            NoneFunc(){
                return
            },
            checkList(){
                const dies1 = document.querySelectorAll('.paginate_item__wrapper__icon__active')
                const h4es1 = document.querySelectorAll('.paginate_item__wrapper__text__active')

                dies1.forEach(element => {
                    element.setAttribute('class', 'paginate_item__wrapper__icon')
                }) 
                
                h4es1.forEach(element => {
                    element.setAttribute('class',  'paginate_item__wrapper__text')
                }) 




                switch(this.$store.state.radioValueText){
                case 'TBC classic':

                        const TBC = document.getElementById('paginate-icon-1')
                        TBC.setAttribute('class','paginate_item__wrapper__icon__active')
                        TBC.nextElementSibling.setAttribute('class','paginate_item__wrapper__text__active')
                    break
                case 'Shadowlands':
                        
                        const Shadowlands = document.getElementById('paginate-icon-6')
                        Shadowlands.setAttribute('class','paginate_item__wrapper__icon__active')
                        Shadowlands.nextElementSibling.setAttribute('class','paginate_item__wrapper__text__active')
                    break
                case 'Torghast':
                        const Torghast = document.getElementById('paginate-icon-2')
                        Torghast.setAttribute('class','paginate_item__wrapper__icon__active')
                        Torghast.nextElementSibling.setAttribute('class','paginate_item__wrapper__text__active')
                    break
                case 'Castle Nathria':
                        const Castle = document.getElementById('paginate-icon-7')
                        Castle.setAttribute('class','paginate_item__wrapper__icon__active')
                        Castle.nextElementSibling.setAttribute('class','paginate_item__wrapper__text__active')
                    break
                case 'Farming':
                        const Farming = document.getElementById('paginate-icon-3')
                        Farming.setAttribute('class','paginate_item__wrapper__icon__active')
                        Farming.nextElementSibling.setAttribute('class','paginate_item__wrapper__text__active')
                    break
                case 'Gearing':
                        const Gearing = document.getElementById('paginate-icon-8')
                        Gearing.setAttribute('class','paginate_item__wrapper__icon__active')
                        Gearing.nextElementSibling.setAttribute('class','paginate_item__wrapper__text__active')
                    break
                case 'Raids':
                        const Raids = document.getElementById('paginate-icon-4')
                        Raids.setAttribute('class','paginate_item__wrapper__icon__active')
                        Raids.nextElementSibling.setAttribute('class','paginate_item__wrapper__text__active')
                    break
                case 'Mounts':
                        const Mounts = document.getElementById('paginate-icon-9')
                        Mounts.setAttribute('class','paginate_item__wrapper__icon__active')
                        Mounts.nextElementSibling.setAttribute('class','paginate_item__wrapper__text__active')
                    break
                case 'PvP':
                        const PvP = document.getElementById('paginate-icon-5')
                        PvP.setAttribute('class','paginate_item__wrapper__icon__active')
                        PvP.nextElementSibling.setAttribute('class','paginate_item__wrapper__text__active')
                    break
                }

                


            
        },
        changePageStore(e) {
            console.clear()

            if(e.target.getAttribute('class') == 'paginate_item__wrapper__icon' || e.target.getAttribute('class') == 'paginate_item__wrapper__icon__active' ){
                

                this.$store.dispatch('switchRadio', Number(e.target.getAttribute('data-value')))
                this.$store.dispatch('RadioType', e.target.nextElementSibling.textContent)

                

                console.log('icon')
            }
            else if(e.target.getAttribute('class') == 'paginate_item__wrapper__text' || e.target.getAttribute('class') == 'paginate_item__wrapper__text__active'){
                this.$store.dispatch('switchRadio', Number(e.target.getAttribute('data-value')))
                this.$store.dispatch('RadioType', e.target.textContent)

                

                console.log('text')
            }


                    const page = document.getElementById('page')
                    const game = document.getElementById('game')
                    const gameMode = document.getElementById('game-mode')

                    page.textContent = this.$store.state.Page
                    game.textContent = this.$store.state.navigateGame
                    console.log(gameMode)

                    gameMode.textContent = this.$store.state.gameMode
            
        },

        },
        watch: {
            '$store.state.radioValueText': function() {
                this.checkList()
            },
        },
        mounted(){
            this.checkList()
        }
    }

</script>

<style lang="scss" scoped>
    .all-content__paginate{
        display: flex;
        justify-content: center;
        margin: 0px auto;
        padding-bottom: 450px;
        
    }
    .box-1,.box-2{
        display: flex;
        flex-direction: column;
    }
    .paginate_item{
        background: #1B1919;
        border-radius: 8px;
        display: flex;
        align-items: center;
        padding:14px 95% 14px 10px;
        padding-left: 10px;
        padding-right: 15px;
        margin-top: 5px;
        cursor: pointer;

        &:first-child{
            margin-top: 0px;
        }
        &__wrapper{
            display: flex;
            align-items: center;
            margin-left: 16px;
            &__icon{
                height: 24px;
                width: 24px;
                opacity: 0.48;
            }
            &__text{
                font-family: 'Quarion';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 135.7%;
                color: #797979;
                margin-left: 6px;
                white-space: nowrap;
            }
        }
    }
    .paginate_item__wrapper__icon__active{
        height: 24px;
        width: 24px;
        opacity: 1;
    }
    
    .paginate_item__wrapper__text__active{
        margin-left: 14px;
        font-family: 'Quarion';
        font-style: normal;
        font-size: 18px;
        line-height: 135.7%;
        background: linear-gradient(90deg, #E5453D -14.78%, #FF7A2E 451.57%);
        -webkit-background-clip: text;
        font-weight: 700;
        -webkit-text-fill-color: transparent;
        background-clip: text;
                white-space: nowrap;
                text-fill-color: transparent;
    }
    #paginate-icon-1{
        background: url(@/assets/TBC-Classic.svg) no-repeat center center / cover;
    
    }
    #paginate-icon-6{
        background: url(@/assets/Shadowlands.svg) no-repeat center center / cover;
    }
    #paginate-icon-2{
        background: url(@/assets/Torghast.svg) no-repeat center center / cover;
    }
    #paginate-icon-7{
        background: url(@/assets/Castle-Nathria.svg) no-repeat center center / cover;
    }
    #paginate-icon-3{
        background: url(@/assets/Farming.svg) no-repeat center center / cover;
    }
    #paginate-icon-8{
        background: url(@/assets/Gearing.svg) no-repeat center center / cover;
    }
    #paginate-icon-4{
        background: url(@/assets/Raids.svg) no-repeat center center / cover;
    }
    #paginate-icon-9{
        background: url(@/assets/Mounts.svg) no-repeat center center / cover;
    }
    #paginate-icon-5{
        background: url(@/assets/PvP.svg) no-repeat center center / cover;
    }
    .box-2{
        margin-left: 8px;

    }
</style>