<template>
    <div class="modal-window" @click.stop="closeModal">
        <div class="modal-window__content">
            <div class="modal-window__content__wrapper" @click.stop>
                <h1 class="title">Service added to cart</h1>
                
                <div class="main-block">
                    <div class="cards-buy" >
                    <transition-group name="card-buy">
                        <cardBuy
                            @remove="RemoveCard"
                            v-if="!this.ModalVersion"
                            v-for="card in $store.state.BuyCards"
                            :card = "card"
                        />
                        <cardBuyMobail
                            @remove="RemoveCard"
                            v-else
                            v-for="card in $store.state.BuyCards"
                            :card = "card"
                        />
                    </transition-group>
                        
                    </div>
                    
                    <div class="totalPrice">
                        <div class="totalAndPrice">
                            <h2>Total</h2>
                            <h3> {{this.TotalPrice}} </h3>
                        </div>
                    </div>
                </div>
                <div class="buttons-block">
                    <div class="buttons">
                        <div class="button-one" @click="ContShop">Continue shopping</div>
                        <div class="button-two" @click="BuyRoute"> chechout</div>
                    </div>
                </div>
                
            </div>
        </div>
    </div>
</template>

<script>
import cardBuy from '@/components/card-buy.vue'
import cardBuyMobail from '@/components/cardBuy-Mobail.vue'

export default{
    data(){
        return{
            ModalVersion: false,
            TotalPrice: 0
        }
    },
    components:{
        cardBuy,
        cardBuyMobail
    },
    methods:{
        RemoveCard(card){
            this.$store.state.BuyCards = this.$store.state.BuyCards.filter(c => c.id !== card.id)
            this.TotalPrice = 0
            this.TotalPric()
        },
        ContShop(){
            this.$router.push('/Store')
        },
        BuyRoute(){
            this.$router.push('/Buy')
            this.$store.dispatch('ModalOpen', false)
        },
        closeModal(){
            this.$store.dispatch('ModalOpen', false)
        },
        TotalPric(){
            let arr = this.$store.state.BuyCards
            arr.forEach(comp => {
                this.TotalPrice = this.TotalPrice + Number(comp.price)
                console.log(comp)
            })
        }
    },
    mounted(){
        if(window.screen.width < 830){this.ModalVersion = true}
        else{this.ModalVersion = false}

        this.TotalPric()
    }
}

</script>

<style lang="scss" scoped>
    .modal-window{
        position: absolute;
        top: 0;left: 0;right: 0;bottom: 0;
        background: rgba(15, 15, 15, 0.64);
        backdrop-filter: blur(4px);
        z-index:150
    }
    .title{
        font-family: 'Quarion';
        font-style: normal;
        font-weight: 600;
        font-size: 22px;
        line-height: 135.7%;
        text-transform: uppercase;
        color: #FFFFFF;
        margin-top: 34px;
        padding-bottom: 37px;

    }
    .modal-window__content{
        position: relative;
        margin: 0 auto;
        max-width: 1000px;
        top: 262px;
        background: #131212;
        box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.85);
        border-radius: 20px;
        &__wrapper{
            width: calc(100% - 34px);
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
    .main-block{
        display: flex;
        align-items: center;
        margin-top: 37px;
        width: 100%;
        margin: 0 auto;
        @media(max-width:830px){
            flex-direction: column;
        }
    }
    .cards-buy{
        width: 100%;
    }
    .totalPrice{
        background: #1B1919;
        border-radius: 10px;
        height: 324px;
        width: 100%;
        max-width: 202px;
        @media(max-width:830px){
            height: 107px;
            max-width: 292px;
            margin-top: 18px;
        }

    }
    .totalAndPrice{
        margin-left: 29px;
        width: 100%;
        margin-top: 40px;
        @media(max-width:830px){
            margin-top: 30px;

        }
        
        h2{
            font-family: 'Inter';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 127.2%;
            letter-spacing: -0.025em;
            color: #5A5A5A;
        }
        h3{
            font-family: 'Quarion';
            font-style: normal;
            font-weight: 600;
            font-size: 30px;
            line-height: 135.7%;
            margin-top: 4px;
            text-transform: uppercase;
            color: #FFFFFF;
            white-space: nowrap;
        }
    }
    .cards-buy{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 742px;
    }
    .button-one{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 127.2%;
        letter-spacing: -0.025em;
        text-transform: uppercase;
        background: linear-gradient(90deg, #E5453D -14.78%, #FF7A2E 451.57%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        cursor: pointer;

        padding:14px 32px;
        border-radius: 8px;
        border:1px solid #F15C38;
        margin-right: 11px;

        @media(max-width:830px){
            margin-right: 0px;
            margin-top: 9px;

        }
        
    }
    .button-two{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 127.2%;
        letter-spacing: -0.025em;
        text-transform: uppercase;
        color: #FFFFFF;
        cursor: pointer;

        padding:15px 53px;
        border-radius: 8px;
        background: linear-gradient(91.07deg, #E5453D -15.2%, #FF7A2E 225.8%);
        

    }
    .buttons-block{
        display: flex;
        justify-content: flex-end;
        max-width: 925px;
        width: 100%;
        padding-bottom: 44px;
        @media(max-width:830px){
            justify-content: center;
            padding-bottom: 36px;
        }
        
    }
    .buttons{
        display: flex;
        align-items: center;
        margin-top: 36px;
        @media(max-width:830px){
            flex-direction: column-reverse;
        }
    }
    .cards-buy{
        max-height: 330px;

        overflow-y: scroll;
        overflow-x: hidden;
        position: relative;
        scrollbar-width: thin;
        scrollbar-color: #ED5D55 #1B1919;

        &::-webkit-scrollbar {
            height: 9px;
            width: 4px;
        }
            &::-webkit-scrollbar-track {
                background: #1B1919;
            }
            &::-webkit-scrollbar-thumb {
                background-color: #ED5D55;
                border-radius: 5px;
                border: 100% solid #ED5D55;
            }
        
    }
    .card-buy-item {
      }
      .card-buy-enter-active,
      .card-buy-leave-active {
        transition: all 0.4s ease;
      }
      .card-buy-enter-from,
      .card-buy-leave-to {
        opacity: 0;
        transform: translateX(130px);
      }
      .card-buy-move {
        transition: transform 0.4s ease;
      }
</style>