<script>
import { on } from 'events'
import $ from 'jquery'

export default{
    name:'nav-bar-3',
    data(){
        return{
            checkItem:'',
            counter: 1
        }
    },
    methods:{
        listOpen(e){
            const listDa =  e.target.parentNode.nextSibling
            const classListDa =  listDa.getAttribute('class')
            const Krest =  e.target.getAttribute('class')

            
            
            if(classListDa == 'Category__wrapper__animate'){
                listDa.setAttribute('class', 'Category__wrapper')
            }
            else{
                listDa.setAttribute('class', 'Category__wrapper__animate') 
            }

            if(Krest == 'nav-item__Krest__active__animate'){
                e.target.setAttribute('class', 'nav-item__Krest__active') 
            }
            else{
                e.target.setAttribute('class', 'nav-item__Krest__active__animate') 

            }

        },
        checkList(){
                const dies1 = document.querySelectorAll('.nav-item__name-group__icon3__active')
                const h4es1 = document.querySelectorAll('.nav-item__name-group__text3__active')
                const KrestFor = document.querySelectorAll('.nav-item__Krest__active')

                dies1.forEach(element => {
                    element.setAttribute('class', 'nav-item__name-group__icon3')
                }) 
                
                h4es1.forEach(element => {
                    element.setAttribute('class',  'nav-item__name-group__text3')
                })
                KrestFor.forEach(element => {
                    element.setAttribute('class',  'nav-item__Krest')
                })




                switch(this.$store.state.radioValueText){
                case 'TBC classic':
                        const TBC = document.getElementById('nav-item__name-group__icon3-one')
                        const TBCKrest = document.getElementById('Krest-1')
                        TBCKrest.setAttribute('class', 'nav-item__Krest__active')

                        TBC.setAttribute('class','nav-item__name-group__icon3__active')
                        TBC.nextElementSibling.setAttribute('class','nav-item__name-group__text3__active')
                    break
                case 'Shadowlands':
                        const Shadowlands = document.getElementById('nav-item__name-group__icon3-two')
                        const ShadowlandsKrest = document.getElementById('Krest-2')
                        ShadowlandsKrest.setAttribute('class', 'nav-item__Krest__active')

                        Shadowlands.setAttribute('class','nav-item__name-group__icon3__active')
                        Shadowlands.nextElementSibling.setAttribute('class','nav-item__name-group__text3__active')
                    break
                case 'Torghast':
                        const Torghast = document.getElementById('nav-item__name-group__icon3-three')
                        const TorghastKrest = document.getElementById('Krest-3')
                        TorghastKrest.setAttribute('class', 'nav-item__Krest__active')

                        Torghast.setAttribute('class','nav-item__name-group__icon3__active')
                        Torghast.nextElementSibling.setAttribute('class','nav-item__name-group__text3__active')
                    break
                case 'Castle Nathria':
                        const Castle = document.getElementById('nav-item__name-group__icon3-four')
                        const CastleKrest = document.getElementById('Krest-4')
                        CastleKrest.setAttribute('class', 'nav-item__Krest__active')

                        Castle.setAttribute('class','nav-item__name-group__icon3__active')
                        Castle.nextElementSibling.setAttribute('class','nav-item__name-group__text3__active')
                    break
                case 'Farming':
                        const Farming = document.getElementById('nav-item__name-group__icon3-five')
                        const FarmingKrest = document.getElementById('Krest-5')
                        FarmingKrest.setAttribute('class', 'nav-item__Krest__active')

                        Farming.setAttribute('class','nav-item__name-group__icon3__active')
                        Farming.nextElementSibling.setAttribute('class','nav-item__name-group__text3__active')
                    break
                case 'Gearing':
                        const Gearing = document.getElementById('nav-item__name-group__icon3-six')
                        const GearingKrest = document.getElementById('Krest-6')
                        GearingKrest.setAttribute('class', 'nav-item__Krest__active')

                        Gearing.setAttribute('class','nav-item__name-group__icon3__active')
                        Gearing.nextElementSibling.setAttribute('class','nav-item__name-group__text3__active')
                    break
                case 'Raids':
                        const Raids = document.getElementById('nav-item__name-group__icon3-seven')
                        const RaidsKrest = document.getElementById('Krest-7')
                        RaidsKrest.setAttribute('class', 'nav-item__Krest__active')

                        Raids.setAttribute('class','nav-item__name-group__icon3__active')
                        Raids.nextElementSibling.setAttribute('class','nav-item__name-group__text3__active')
                    break
                case 'Mounts':
                        const Mounts = document.getElementById('nav-item__name-group__icon3-eight')
                        const MountsKrest = document.getElementById('Krest-8')
                        MountsKrest.setAttribute('class', 'nav-item__Krest__active')

                        Mounts.setAttribute('class','nav-item__name-group__icon3__active')
                        Mounts.nextElementSibling.setAttribute('class','nav-item__name-group__text3__active')
                    break
                case 'PvP':
                        const PvP = document.getElementById('nav-item__name-group__icon3-nine')
                        const PvPKrest = document.getElementById('Krest-9')
                        PvPKrest.setAttribute('class', 'nav-item__Krest__active')

                        PvP.setAttribute('class','nav-item__name-group__icon3__active')
                        PvP.nextElementSibling.setAttribute('class','nav-item__name-group__text3__active')
                    break
                }
        },
         changePageStore(e) {

            if(e.target.getAttribute('class') == 'nav-item__name-group__icon3' || e.target.getAttribute('class') == 'nav-item__name-group__icon3__active' ){

                this.$store.dispatch('switchRadio', Number(e.target.getAttribute('data-value')))
                this.$store.dispatch('RadioType', e.target.nextElementSibling.textContent)


            }
            else if(e.target.getAttribute('class') == 'nav-item__name-group__text3' || e.target.getAttribute('class') == 'nav-item__name-group__text3__active'){
                this.$store.dispatch('switchRadio', Number(e.target.getAttribute('data-value')))
                this.$store.dispatch('RadioType', e.target.textContent)


            }

                    // document.getElementById('span-2').setAttribute('class', 'arrow')

                    // const page = document.getElementById('page')
                    // const game = document.getElementById('game')
                    // const gameMode = document.getElementById('game-mode')

                    // page.textContent = this.$store.state.Page
                    // game.textContent = this.$store.state.navigateGame

                    // gameMode.textContent = this.$store.state.gameMode
            
        },
    },  

       
        watch: {
        '$store.state.radioValueText': function() {
            this.checkList()
        }
    },

    mounted(){
        this.checkList()
    }

}

</script>

<style lang="scss" scoped>
