}
#nav-item__name-group__icon3-nine{
    background: url(@/assets/PvP.svg) no-repeat center center / cover;
}
















.nav-item__Krest{

}
.nav-item__Krest__active{
    background: url(@/assets/Saidbar-Krest.svg) no-repeat center center / cover;
    height: 15px;
    width: 15px;
    margin-right: 27px;
    opacity: 1;
    cursor: pointer;
    transform: rotate(360deg);
    animation: KrestOpen 1s ease;

}
.nav-item__Krest__active__animate{
    background: url(@/assets/Saidbar-Krest.svg) no-repeat center center / cover;
    height: 15px;
    width: 15px;
    margin-right: 27px;
    opacity: 1;
    transform: rotate(0deg);
    cursor: pointer;
    animation: KrestClose 1s ease;
}
.Category__list{
    list-style: none;
    margin-right: 70px;
    height: auto;

}
.Category__list__item{
    margin-top: 17px;
    font-family: 'Quarion';
    font-style: normal;
    font-weight: 400;
    white-space: nowrap;
    font-size: 18px;
    line-height: 135.7%;
    color: #989898;
        cursor: pointer;


    &:first-child{
        margin-top: 22px;
    }
}
.Category__list__animate{
    list-style: none;
    margin-right: 70px;
}
.Category__wrapper{
    width: 100%;
    display: flex;
    justify-content: center;
    height: 171px;
    animation: listOpen 1s ease;
    overflow: hidden;

}
.Category__wrapper__animate{
    width: 100%;
    height: 0px;
    display: flex;
    overflow: hidden;
    animation: listClose 1s ease;
    justify-content: center;
}

.Category__list__item__active{

}
@keyframes listClose{
    from{
        height: 171px;
    }
    to{
        height: 0px;

    }
}
@keyframes listOpen{
    from{
        height: 0px;
    }
    to{
        height: 171px;

    }
}
@keyframes KrestClose{
    from{
        transform: rotate(360deg);
    }
    to{
        transform: rotate(0deg);

    }
} 
@keyframes KrestOpen{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);

    }
} 

</style>