<script>
import $ from 'jquery';

export default{
    data(){
            return{
                droplistDate: 'Monday',
                droplistTime: '19:00',
                optionOne:{'name' : '', 'price' : '0'},
                optionTwo:{'name' : '', 'price' : '0'},
                optionThree:{'name' : '', 'price' : '0'},
                optionDLC: {'name' : '', 'price' : '0'},
                beforeOptions: [],
                price:0,
            }
        },
    methods:{
        addOption1(e){
            let targetAfterName, targetAfterPrice;
            const target = e.target
            if(target.getAttribute('class') == 'Price'){
                console.log(target)
                targetAfterName = e.target.previousSibling
                targetAfterPrice = e.target
            }
            else if(target.getAttribute('class') == 'Boss-name'){
                targetAfterName = e.target
                targetAfterPrice = e.target.nextSibling
            }
            else if(target.getAttribute('data-radio') == 'true' || target.getAttribute('data-radio') == 'false'){
                targetAfterName = e.target.firstChild.firstChild
                targetAfterPrice = e.target.firstChild.firstChild.nextSibling
            }
            else if(target.getAttribute('class') == 'Boss-name_And_Price'){
                targetAfterName = e.target.firstChild
                targetAfterPrice = e.target.firstChild.nextSibling
            }
            this.optionOne.name = targetAfterName.textContent
            let fullPrice = ''
            for(let char of targetAfterPrice.textContent){

                if(char == ' '){
                    continue
                }
                if(char == ''){
                    continue
                }
                fullPrice = fullPrice + char 
            }
            let OptionOne = fullPrice

            this.optionOne.price = OptionOne
            this.priceSum()
        },
        addOption2(e){
            if(e.target.getAttribute('class') != 'quest' && e.target.getAttribute('class') != 'quest_content'   && e.target.getAttribute('class') != 'quest_content__text'){
                let targetAfterName, targetAfterPrice;
                const target = e.target
                if(target.getAttribute('class') == 'Price'){
                    console.log(target)
                    targetAfterName = e.target.previousSibling
                    targetAfterPrice = e.target
                }
                else if(target.getAttribute('class') == 'Boss-name'){
                    targetAfterName = e.target
                    targetAfterPrice = e.target.nextSibling
                }
                else if(target.getAttribute('data-radio') == 'true' || target.getAttribute('data-radio') == 'false'){
                    targetAfterName = e.target.firstChild.firstChild
                    targetAfterPrice = e.target.firstChild.firstChild.nextSibling
                }
                else if(target.getAttribute('class') == 'Boss-name_And_Price'){
                    targetAfterName = e.target.firstChild
                    targetAfterPrice = e.target.firstChild.nextSibling
                }
                this.optionTwo.name = targetAfterName.textContent

                let fullPrice = ''
                for(let char of targetAfterPrice.textContent){

                    if(char == ' '){
                        continue
                    }
                    if(char == ''){
                        continue
                    }
                    fullPrice = fullPrice + char 
                }
                let OptionOne = fullPrice

                this.optionTwo.price = OptionOne
                this.priceSum()
            }
        },
        addOption3(e){
            let targetAfterName, targetAfterPrice;
            const target = e.target
            if(target.getAttribute('class') == 'Price'){
                console.log(target)
                targetAfterName = e.target.previousSibling
                targetAfterPrice = e.target
            }
            else if(target.getAttribute('class') == 'Boss-name'){
                targetAfterName = e.target
                targetAfterPrice = e.target.nextSibling
            }
            else if(target.getAttribute('data-radio') == 'true' || target.getAttribute('data-radio') == 'false'){
                targetAfterName = e.target.firstChild.firstChild
                targetAfterPrice = e.target.firstChild.firstChild.nextSibling
            }
            else if(target.getAttribute('class') == 'Boss-name_And_Price'){
                targetAfterName = e.target.firstChild
                targetAfterPrice = e.target.firstChild.nextSibling
            }
            this.optionThree.name = targetAfterName.textContent

            let fullPrice = ''
                for(let char of targetAfterPrice.textContent){

                    if(char == ' '){
                        continue
                    }
                    if(char == ''){
                        continue
                    }
                    fullPrice = fullPrice + char 
                }
                let OptionOne = fullPrice

                this.optionThree.price = OptionOne
                this.priceSum()
        },
        addOption4(){
            if(this.optionDLC.price == '0'){
                this.optionDLC.price = '25'

            }
            else if(this.optionDLC.price == '25'){
                this.optionDLC.price = '0'

            }
            this.priceSum()

             
        },
        HorAll(e){
            const Horde = document.getElementById('Horde')
            const Alliance = document.getElementById('Alliance')
            const wrapper = document.getElementById('HorAllWrapper')

            if(e.target == Horde && Horde.hasAttribute('data-Vibor_disable')){

                wrapper.setAttribute('class', 'Horde_Or_Alliance__wrapper')
                
                Horde.removeAttribute('data-Vibor_disable')
                Horde.setAttribute('data-Vibor_active', 'true')

                Alliance.removeAttribute('data-Vibor_active')
                Alliance.setAttribute('data-Vibor_disable', 'true')

                Horde.setAttribute('class', 'Horde_active')
                Alliance.setAttribute('class', 'Alliance_disable')
                
            }
            if(e.target == Alliance &&  Alliance.hasAttribute('data-Vibor_disable')){

                wrapper.setAttribute('class', 'Horde_Or_Alliance__wrapper_animate')

                Alliance.removeAttribute('data-Vibor_disable')
                Alliance.setAttribute('data-Vibor_active', 'true')

                Horde.removeAttribute('data-Vibor_active')
                Horde.setAttribute('data-Vibor_disable', 'true')

                Horde.setAttribute('class', 'Horde_disable')
                Alliance.setAttribute('class', 'Alliance_active')
            }
        },

        listToggle(){
            $(function(){
                if ($(".droplist-body").attr("data-toggle") == "false") {

                    $(".list-arrow_two").css({ "transform": "rotate(0deg)" });
                    $(".droplist-body").animate({
                        height: "0",
                        paddingBottom: '0'
                    }, 400, "linear");
                    $(".droplist-body").attr("data-toggle", "true");
                }
                else if($(".droplist-body").attr("data-toggle") == "true"){

                    $(".list-arrow_two").css({ "transform": "rotate(180deg)" });
                    $(".droplist-body").animate({
                        height: "205",
                        paddingBottom: '16'
                    }, 400, "linear");
                    $(".droplist-body").attr("data-toggle", "false");
                }
            })
        },
        dataPick(e){
            const target = e.target
            if(target.getAttribute('class') == 'droplist__date__disabled'){
                const oldEl = document.querySelectorAll('.droplist__date__active')
                oldEl.forEach((element) => {
                    element.setAttribute('class', 'droplist__date__disabled')
                })
                target.setAttribute('class', 'droplist__date__active')
                const slpitDate = target.textContent.split(' ')
                this.droplistDate = slpitDate[0]
                this.droplistTime = slpitDate[1]
            }
            else if(target.getAttribute('class') == 'droplist__date__active'){
                const slpitDate = target.textContent.split(' ')
                this.droplistDate = slpitDate[0]
                this.droplistTime = slpitDate[1]
            }
            
        },
        BoostPick(e){
            const SelfPlay = document.getElementById('Self-play')
            const Piloted = document.getElementById('Piloted')
            const Wrapper = document.getElementById('BoostingModeWrapper')

            if(e.target == SelfPlay && SelfPlay.hasAttribute('data-Pick_disable')){

                Wrapper.setAttribute('class', 'Boosting-mode__wrapper')
                
                SelfPlay.removeAttribute('data-Pick_disable')
                SelfPlay.setAttribute('data-Pick_active', 'true')

                Piloted.removeAttribute('data-Pick_active')
                Piloted.setAttribute('data-Pick_disable', 'true')

                SelfPlay.setAttribute('class', 'Self-play_active')
                Piloted.setAttribute('class', 'Piloted_disable')
                
            }
            if(e.target == Piloted &&  Piloted.hasAttribute('data-Pick_disable')){

                Wrapper.setAttribute('class', 'Boosting-mode__wrapper_animate')

                Piloted.removeAttribute('data-Pick_disable')
                Piloted.setAttribute('data-Pick_active', 'true')

                SelfPlay.removeAttribute('data-Pick_active')
                SelfPlay.setAttribute('data-Pick_disable', 'true')

                SelfPlay.setAttribute('class', 'Self-play_disable')
                Piloted.setAttribute('class', 'Piloted_active')
            }
        },
        Maincraft(e){
            e.preventDefault()
            const El = e.target.nextSibling
            const content = document.querySelectorAll('.quest_content')
            const targets = document.querySelectorAll('.quest')

            if(e.target.getAttribute('data-target') == 'false'){
                content.forEach((comp) => {
                    comp.style.opacity = 0.0
                })
                El.style.opacity = 1

                targets.forEach((Elem) => {
                    Elem.setAttribute('data-target', 'false')
                })
                e.target.setAttribute('data-target', 'true')
            }
            else if(e.target.getAttribute('data-target') == 'true'){
                content.forEach((comp) => {
                    comp.style.opacity = 0.0
                })
                targets.forEach((Elem) => {
                    Elem.setAttribute('data-target', 'false')
                })


            }
        },
        ModalOpen(){
            let arr = []

            this.$store.dispatch('ModalOpen', true)
            if(this.optionOne.name != ''){
                arr.push(this.optionOne)
            }
            if(this.optionTwo.name != ''){
                arr.push(this.optionTwo)
            }
            if(this.optionThree.name != ''){
                arr.push(this.optionThree)
            }


            console.log()
            
            const Obj =
            {
                'title' : this.$store.state.PageCard[this.$store.state.ItemName - 1].cardTitle,
                'background' : this.$store.state.PageCard[this.$store.state.ItemName - 1].background,
                'price' : this.price,
                'options': arr,
                'id' : this.$store.state.BuyCardsCounter
            }
            this.$store.state.BuyCards.push(Obj)
            console.log(this.$store.state.BuyCards)

            this.$store.state.BuyCardsCounter ++

            setTimeout(() => {
                this.optionOne = {'name' : '', 'price' : '0'}
                this.optionTwo = {'name' : '', 'price' : '0'}
                this.optionThree = {'name' : '', 'price' : '0'}
                this.optionDLC = {'name' : '', 'price' : '0'}
            },2000)
            
            
        },
        priceSum(){
            let sum = Number(this.optionOne.price) + Number(this.optionTwo.price) + Number(this.optionThree.price) + Number(this.$store.state.PageCard[this.$store.state.ItemName - 1].price) + Number(this.optionDLC.price)
            this.price = sum
            sum = 0
        }
    },
    mounted(){
        this.price = this.$store.state.PageCard[this.$store.state.ItemName - 1].price
    }
}

</script>

