<template>
    <div class="BuyPage">
        <div class="_container">
            <psevdo-navbar></psevdo-navbar>
            <main>
                <div class="cards-buy" >
                    <transition-group name="card-buy">
                        <cardBuy
                            @remove="RemoveCard"
                            v-if="!this.ModalVersion"
                            v-for="card in $store.state.BuyCards"
                            :card = "card"
                        />
                        <cardBuyMobail
                            @remove="RemoveCard"
                            v-else
                            v-for="card in $store.state.BuyCards"
                            :card = "card"
                        />
                    </transition-group>
                        
                </div>
                <div class="TotalAndPromo">
                    <div class="TotalAndPromo__wrapper">
                        <div class="Total">
                            <h2>Total</h2>
                            <h3> {{this.TotalPrice}} </h3>
                        </div>
                        <div class="Coupon">
                            <div class="subTitle">Coupon code</div>

                            <div class="coupon-cont">

                                <input
                                type="text"
                                class="Coupon-input"
                                v-focus
                                @input="codeCheack"
                                v-model="this.inputText"
                                >
                                
                                

                                <div class="Ok-button" v-if="this.promoState === 'Ok'">
                                    <span class="Ok-button__OK"></span>
                                </div>

                                <div class="No-button" v-else-if="this.promoState === 'No'">
                                    <span class="No-button__NO"></span>
                                </div>

                                <div class="next-button" v-else>
                                    <span class="next-button__arrow"></span>
                                </div>

                            </div>
                            <div class="Code-valid" v-if="this.promoState !== 'next'">
                                <h3 v-if="this.promoState === 'Ok'">Code {{this.codeApl}} applied</h3>
                                <h4 v-if="this.promoState === 'No'">{{this.codeApl}} cannot be applied to this product </h4>
                            </div>
                        </div>
                        <a class="buy-now" @click="this.$router.push('/Pay')">Proceed chechout</a>
                    </div>
                </div>
            </main>
        </div>
    </div>
</template>

<script>
import cardBuy from '@/components/card-buy.vue'
import cardBuyMobail from '@/components/cardBuy-Mobail.vue'

export default{
    data(){
        return{
            TotalPrice: 0,
            promoState:'No',
            codeApl: 'Welcome15',
            inputText:'',
            ModalVersion:false

        }
    },
    components:{
        cardBuy,
        cardBuyMobail
    },
    methods:{
        RemoveCard(card){
                
        this.$store.state.BuyCards = this.$store.state.BuyCards.filter(c => c.id !== card.id)
        this.TotalPrice = 0
        this.TotalPric()
            
            
        },
        TotalPric(){
            let arr = this.$store.state.BuyCards
            arr.forEach(comp => {
                this.TotalPrice = Number(this.TotalPrice) + Number(comp.price)
            })
        },
        codeCheack(){
            if(this.inputText === ''){
                this.promoState = ''
            }
            else if(this.inputText === this.$store.state.promoCode){
                this.promoState = 'Ok'

            }
            else{
                this.promoState = 'No'

            }
            
        }
    },
    mounted(){
        this.TotalPric()
        this.codeCheack()
        if(window.screen.width < 830){this.ModalVersion = true}
        else{this.ModalVersion = false}
    }
}

</script>

<style lang="scss" scoped>
    .BuyPage{
        background: #131212;
        width: 100%;
        min-height: 100vh;
    }
    .cards-buy{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 742px;
    }
    main{
        display: flex;
        margin-top: 32px;
        width: 100%;
        @media(max-width:830px){
            flex-direction: column;
            margin: 0 auto;
            align-items: center;
        }
    }
    .card-buy-item {
    }
    .card-buy-enter-active,
    .card-buy-leave-active {
      transition: all 0.4s ease;
    }
    .card-buy-enter-from,
    .card-buy-leave-to {
      opacity: 0;
      transform: translateX(130px);
    }
    .card-buy-move {
      transition: transform 0.4s ease;
    }
    .Total{
        h2{
            font-family: 'Inter';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 127.2%;
            letter-spacing: -0.025em;
            color: #5A5A5A;
        }
        h3{
            font-family: 'Quarion';
            font-style: normal;
            font-weight: 600;
            font-size: 30px;
            line-height: 135.7%;
            margin-top: 4px;
            text-transform: uppercase;
            color: #FFFFFF;
            white-space: nowrap;
        }
    }
    .TotalAndPromo{
        margin-top: 30px;
        margin-left: 30px;
        background: #1B1919;
        border-radius: 10px;
        max-height: 305px;

        &__wrapper{
            padding: 30px 15px 51px 23px;
        }
    }
    .subTitle{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 127.2%;
        letter-spacing: -0.025em;
        color: #5A5A5A;
        margin-top: 18px;
    }
    .Coupon{
        padding-bottom: 45px;
    }
    .Coupon-input{
        margin-left: 19px;
        background: none;
        border: transparent;
        
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        letter-spacing: -0.025em;
        color: #FFFFFF;

        caret-color: #4C4C4C;
    }
    .coupon-cont{
        background: #1D1D1D;
        border-radius: 8px;
        display: flex;
        align-items: center;
        margin-top: 6px;
        height: 49px;
    }
    .next-button{
        background: linear-gradient(90deg, #E5453D -14.78%, #FF7A2E 451.57%);
        border-radius: 5px;
        transform: rotate(90deg);
        height: 35px;
        width: 35px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 22px;
        cursor: pointer;

        &__arrow{
            width: 7px;
            height: 10px;
            background: #FFFFFF;
            clip-path: polygon(100% 0, 0 50%, 100% 100%);
            transform: rotate(90deg);
        }
    }
    .Ok-button{
        background: linear-gradient(180deg, #51B37F 0%, #298152 100%), linear-gradient(90deg, #E5453D -14.78%, #FF7A2E 451.57%);
        border-radius: 5px;

        height: 38px;
        width: 38px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 22px;

        &__OK{
            width: 13px;
            height: 9px;
            background: url(@/assets/Ok.svg) no-repeat center center / cover;
            
        }
    }
    .No-button{
        background:  linear-gradient(90deg, #E5453D -14.78%, #FF7A2E 451.57%);
        border-radius: 5px;

        height: 38px;
        width: 38px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 22px;

        &__NO{
            width: 16px;
            height: 16px;
            background: url(@/assets/No.svg) no-repeat center center / cover;
            
        }
    }
    .Code-valid{
        h3{
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 127.2%;
            letter-spacing: -0.025em;
            color: #51B37F;
            margin-top: 4px;
        }
        h4{
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 127.2%;
            letter-spacing: -0.025em;
            margin-top: 4px;
            color: #F1433A;
        }
    }
    .buy-now{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 127.2%;
        letter-spacing: -0.025em;
        color: #FFFFFF;
        text-transform: uppercase;
        white-space: nowrap;
        padding:14px 68px;
        border:none;
        background: linear-gradient(91.07deg, #E5453D -15.2%, #FF7A2E 225.8%);
        border-radius: 8px;
        margin-top: 27px;
        cursor: pointer;
    }
    .buy-now:hover{
            transition: all 0.2s;
            background: linear-gradient(91.07deg, #FF3B32 -15.2%, #FF5C00 225.8%);
        }
    .buy-now:active{
        background: linear-gradient(90deg, #E5453D -14.78%, #FF7A2E 451.57%);
    }
</style>