<template>
    <ul class="nav-list">

        <li class="nav-item" @click="changePageStore">
            <div class="nav-item__icon" id="nav-item__icon-one" data-value="1"></div>
            <h4 class="nav-item__text" data-value="1">TBC Classic</h4>
        </li>
        <li class="nav-item" @click="changePageStore">
            <div class="nav-item__icon" id="nav-item__icon-two" data-value="2"></div>
            <h4 class="nav-item__text" data-value="2">Shadowlands</h4>
        </li>
        <li class="nav-item" @click="changePageStore">
            <div class="nav-item__icon" id="nav-item__icon-three" data-value="3"></div>
            <h4 class="nav-item__text" data-value="3">Torghast</h4>
        </li>
        <li class="nav-item" @click="changePageStore">
            <div class="nav-item__icon" id="nav-item__icon-four" data-value="4"></div>
            <h4 class="nav-item__text" data-value="4">Castle Nathria</h4>
        </li>
            <li class="nav-item" @click="changePageStore">
            <div class="nav-item__icon" id="nav-item__icon-five" data-value="5"></div>
            <h4 class="nav-item__text" data-value="5">Farming</h4>
        </li>
        <li class="nav-item" @click="changePageStore">
            <div class="nav-item__icon" id="nav-item__icon-six" data-value="6"></div>
            <h4 class="nav-item__text" data-value="6">Gearing</h4>
        </li>
        <li class="nav-item" @click="changePageStore">
            <div class="nav-item__icon" id="nav-item__icon-seven" data-value="7"></div>
            <h4 class="nav-item__text" data-value="7">Raids</h4>
        </li>
        <li class="nav-item" @click="changePageStore">
            <div class="nav-item__icon" id="nav-item__icon-eight" data-value="8"></div>
            <h4 class="nav-item__text" data-value="8">Mounts</h4>
        </li>
        <li class="nav-item" @click="changePageStore">
            <div class="nav-item__icon" id="nav-item__icon-nine" data-value="9"></div>
            <h4 class="nav-item__text" data-value="9">PvP</h4>
        </li>

    </ul>
</template>

<script>
import $ from 'jquery'

export default{
    name:'nav-bar-2',
    data(){
        return{
            checkItem:''
        }
    },
    methods:{
        checkList(){
                const dies1 = document.querySelectorAll('.nav-item__icon__active')
                const h4es1 = document.querySelectorAll('.nav-item__text__active')

                dies1.forEach(element => {
                    element.setAttribute('class', 'nav-item__icon')
                }) 
                
                h4es1.forEach(element => {
                    element.setAttribute('class',  'nav-item__text')
                }) 

                console.log(this.$store.state.radioValueText)


                switch(this.$store.state.radioValueText){
                case 'TBC classic':

                        const TBC = document.getElementById('nav-item__icon-one')
                        TBC.setAttribute('class','nav-item__icon__active')
                        TBC.nextElementSibling.setAttribute('class','nav-item__text__active')
                    break
                case 'Shadowlands':
                        
                        const Shadowlands = document.getElementById('nav-item__icon-two')
                        Shadowlands.setAttribute('class','nav-item__icon__active')
                        Shadowlands.nextElementSibling.setAttribute('class','nav-item__text__active')
                    break
                case 'Torghast':
                        const Torghast = document.getElementById('nav-item__icon-three')
                        Torghast.setAttribute('class','nav-item__icon__active')
                        Torghast.nextElementSibling.setAttribute('class','nav-item__text__active')
                    break
                case 'Castle Nathria':
                        const Castle = document.getElementById('nav-item__icon-four')
                        Castle.setAttribute('class','nav-item__icon__active')
                        Castle.nextElementSibling.setAttribute('class','nav-item__text__active')
                    break
                case 'Farming':
                        const Farming = document.getElementById('nav-item__icon-five')
                        Farming.setAttribute('class','nav-item__icon__active')
                        Farming.nextElementSibling.setAttribute('class','nav-item__text__active')
                    break
                case 'Gearing':
                        const Gearing = document.getElementById('nav-item__icon-six')
                        Gearing.setAttribute('class','nav-item__icon__active')
                        Gearing.nextElementSibling.setAttribute('class','nav-item__text__active')
                    break
                case 'Raids':
                        const Raids = document.getElementById('nav-item__icon-seven')
                        Raids.setAttribute('class','nav-item__icon__active')
                        Raids.nextElementSibling.setAttribute('class','nav-item__text__active')
                    break
                case 'Mounts':
                        const Mounts = document.getElementById('nav-item__icon-eight')
                        Mounts.setAttribute('class','nav-item__icon__active')
                        Mounts.nextElementSibling.setAttribute('class','nav-item__text__active')
                    break
                case 'PvP':
                        const PvP = document.getElementById('nav-item__icon-nine')
                        PvP.setAttribute('class','nav-item__icon__active')
                        PvP.nextElementSibling.setAttribute('class','nav-item__text__active')
                    break
                }

                
                console.log(this.$store.state.radioValueText)

            
        },
        changePageStore(e) {
            console.clear()

            if(e.target.getAttribute('class') == 'nav-item__icon' || e.target.getAttribute('class') == 'nav-item__icon__active' ){
                console.log('message' + e.target.getAttribute('data-value'))
                console.log('message2' + e.target.nextElementSibling.textContent)

                this.$store.dispatch('switchRadio', Number(e.target.getAttribute('data-value')))
                this.$store.dispatch('RadioType', e.target.nextElementSibling.textContent)

                console.log(this.$store.state.radioValueText)
                console.log(this.$store.state.radioValue)

                console.log('icon')
            }
            else if(e.target.getAttribute('class') == 'nav-item__text' || e.target.getAttribute('class') == 'nav-item__text__active'){
                this.$store.dispatch('switchRadio', Number(e.target.getAttribute('data-value')))
                this.$store.dispatch('RadioType', e.target.textContent)

                console.log(this.$store.state.radioValueText)
                console.log(this.$store.state.radioValue)

                console.log('text')
            }

                    document.getElementById('span-2').setAttribute('class', 'arrow')

                    const page = document.getElementById('page')
                    const game = document.getElementById('game')
                    const gameMode = document.getElementById('game-mode')

                    page.textContent = this.$store.state.Page
                    game.textContent = this.$store.state.navigateGame
                    console.log(gameMode)

                    gameMode.textContent = this.$store.state.gameMode
        },
    },  
    watch: {
        '$store.state.radioValueText': function() {
            this.checkList()
        }
    },
    mounted(){
    }

}

</script>

<style lang="scss" scoped>
    .nav-list{
        list-style: none;

    .nav-item{
        margin-top: 41px;
        display: flex;
        align-items: center;
        cursor: pointer;

        &:first-child{
            margin-top: 25px;
        }

        &__text{
            margin-left: 14px;
            font-family: 'Quarion';
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 135.7%;
            color: #797979;
        }
        &__icon{
            height: 24px;
            width: 24px;
            opacity: 0.48;
        }
        
    }
}
.nav-item__icon__active{
    height: 24px;
    width: 24px;
    opacity: 1;
}

.nav-item__text__active{
    margin-left: 14px;
    font-family: 'Quarion';
    font-style: normal;
    font-size: 18px;
    line-height: 135.7%;
    background: linear-gradient(90deg, #E5453D -14.78%, #FF7A2E 451.57%);
    -webkit-background-clip: text;
    font-weight: 700;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}
#nav-item__icon-one{
    background: url(@/assets/TBC-Classic.svg) no-repeat center center / cover;

}
#nav-item__icon-two{
    background: url(@/assets/Shadowlands.svg) no-repeat center center / cover;
}
#nav-item__icon-three{
    background: url(@/assets/Torghast.svg) no-repeat center center / cover;
}
#nav-item__icon-four{
    background: url(@/assets/Castle-Nathria.svg) no-repeat center center / cover;
}
#nav-item__icon-five{
    background: url(@/assets/Farming.svg) no-repeat center center / cover;
}
#nav-item__icon-six{
    background: url(@/assets/Gearing.svg) no-repeat center center / cover;
}
#nav-item__icon-seven{
    background: url(@/assets/Raids.svg) no-repeat center center / cover;
}
#nav-item__icon-eight{
    background: url(@/assets/Mounts.svg) no-repeat center center / cover;
}
#nav-item__icon-nine{
    background: url(@/assets/PvP.svg) no-repeat center center / cover;
}
</style>