<template>
    <section class="store-page" id="store-page">
    <burger-menu></burger-menu>
    <Navbar @CheckBurgerMenu="CheckBurgerMenu"></Navbar>
    <div class="osnov-block">
        <Sidebar ></Sidebar>
        <StoreMain style="width: 100%;"></StoreMain>
    </div>
    <footer-block style="padding-top:50px"></footer-block>

    </section>
</template>

<script>

import Navbar from '@/components/UI/Navbar.vue'
import Sidebar from '@/components/side-bar.vue'
import StoreMain from '@/components/Store-main.vue'
export default{
    components:{
        Navbar,
        Sidebar,
        StoreMain,
    },
    methods:{
CheckBurgerMenu(){
            const burgerMenu = document.querySelector('#burger-menu')
            const header = document.querySelector('#store-page')
            const burgerCont = document.querySelector('#burger-cont')
            const mainBlur = document.querySelector('#blur__main')
            const burgerMenuKrest = document.querySelector('#Burger-menu__Krest')
            
                burgerMenu.style.transform = 'translateX(0%)'
                header.style.overflow = 'visible'
                burgerCont.setAttribute('class', 'burger-menu_cont__active')
                mainBlur.setAttribute('class', 'blur__main__active')
                            
            
                burgerCont.addEventListener('click', (e) => {
                    if(e.target == burgerCont || e.target == burgerMenuKrest){
                        burgerMenu.style.transform = 'translateX(100%)'
                        header.style.overflow = 'hidden'
                        burgerCont.setAttribute('class', 'burger-menu_cont__disable')
                        mainBlur.setAttribute('class', 'blur__main__disable')
                    }
                    else{
                    }

                },false) 
        }
    },

}

</script>

<style lang="scss" scoped>

    .osnov-block{
        display: flex;
    }
    .store-page{
        background: #141414;
                overflow: hidden;
        width: 100%;
        position: relative;

        z-index: 99;
    }

</style>